export default {
	name: 'uiOneInput',
	props: {
		// 4/6
		maxlength: {
			type: Number,
			default: 6
		},
		isPwd: {
			type: Boolean,
			default: false
		},
		// middle-middle line, bottom-bottom line, box-square box
		type: {
			type: String,
			default: "line"
		}
	},
	watch: {
		maxlength: {
			immediate: true,
			handler: function(newV) {
				if (newV === 6) {
					this.ranges = [1, 2, 3, 4, 5, 6]
				} else {
					this.ranges = [1, 2, 3, 4]
				}
			}
		}
	},
	data() {
		return {
			codeIndex: 1,
			codeArr: [],
			ranges: [1, 2, 3, 4],
		}
	},
	methods: {
		getVal(e) {
			let { value } = e.detail
			let arr = value.split('')
			this.codeIndex = arr.length + 1
			this.codeArr = arr
			this.$emit('finish',this.codeArr.join(''))
		}
	}
}
